/* Classes for background gradient. the number following 't' stands for the hour in the 24h system. */
.t0,
.t1,
.t2,
.t22,
.t23,
.t24,
.t3,
.t4 {
  background: #182379;
  background: -webkit-gradient(linear, left top, left bottom, from(#0B121E), to(#182379));
  background: -webkit-linear-gradient(top, #0B121E 0%, #182379 100%);
  background: -o-linear-gradient(top, #0B121E 0%, #182379 100%);
  background: linear-gradient(to bottom, #0B121E 0%, #182379 100%);
  background-repeat: no-repeat;
  background-attachment: fixed; }

.t5 {
  background: #448388;
  background: -webkit-gradient(linear, left top, left bottom, from(#0B2145), to(#448388));
  background: -webkit-linear-gradient(top, #0B2145 0%, #448388 100%);
  background: -o-linear-gradient(top, #0B2145 0%, #448388 100%);
  background: linear-gradient(to bottom, #0B2145 0%, #448388 100%);
  background-repeat: no-repeat;
  background-attachment: fixed; }

.t6,
.t7 {
  background: #F3A350;
  background: -webkit-gradient(linear, left top, left bottom, from(#10459f), color-stop(99%, #F3A350));
  background: -webkit-linear-gradient(top, #10459f 0%, #F3A350 99%);
  background: -o-linear-gradient(top, #10459f 0%, #F3A350 99%);
  background: linear-gradient(to bottom, #10459f 0%, #F3A350 99%);
  background-repeat: no-repeat;
  background-attachment: fixed; }

.t10,
.t8,
.t9 {
  background: #f5da7b;
  background: -webkit-gradient(linear, left top, left bottom, from(#3527e9), color-stop(99%, #f5da7b));
  background: -webkit-linear-gradient(top, #3527e9 0%, #f5da7b 99%);
  background: -o-linear-gradient(top, #3527e9 0%, #f5da7b 99%);
  background: linear-gradient(to bottom, #3527e9 0%, #f5da7b 99%);
  background-repeat: no-repeat;
  background-attachment: fixed; }

.t11,
.t12,
.t13,
.t14,
.t15,
.t16 {
  background: #F5CE7E;
  background: -webkit-gradient(linear, left top, left bottom, from(#0E5BEE), color-stop(99%, #F5CE7E));
  background: -webkit-linear-gradient(top, #0E5BEE 0%, #F5CE7E 99%);
  background: -o-linear-gradient(top, #0E5BEE 0%, #F5CE7E 99%);
  background: linear-gradient(to bottom, #0E5BEE 0%, #F5CE7E 99%);
  background-repeat: no-repeat;
  background-attachment: fixed; }

.t17 {
  background: #eeaf61;
  background: -webkit-gradient(linear, left top, left bottom, from(#0d55d7), color-stop(99%, #eeaf61));
  background: -webkit-linear-gradient(top, #0d55d7 0%, #eeaf61 99%);
  background: -o-linear-gradient(top, #0d55d7 0%, #eeaf61 99%);
  background: linear-gradient(to bottom, #0d55d7 0%, #eeaf61 99%);
  background-repeat: no-repeat;
  background-attachment: fixed; }

.t18,
.t19 {
  background: #B48394;
  background: -webkit-gradient(linear, left top, left bottom, from(#78A7D6), to(#B48394));
  background: -webkit-linear-gradient(top, #78A7D6 0%, #B48394 100%);
  background: -o-linear-gradient(top, #78A7D6 0%, #B48394 100%);
  background: linear-gradient(to bottom, #78A7D6 0%, #B48394 100%);
  background-repeat: no-repeat;
  background-attachment: fixed; }

.t20,
.t21 {
  background: #F3A350;
  background: -webkit-gradient(linear, left top, left bottom, from(#10459f), color-stop(99%, #F3A350));
  background: -webkit-linear-gradient(top, #10459f 0%, #F3A350 99%);
  background: -o-linear-gradient(top, #10459f 0%, #F3A350 99%);
  background: linear-gradient(to bottom, #10459f 0%, #F3A350 99%);
  background-repeat: no-repeat;
  background-attachment: fixed; }

@media screen and (max-height: 500px) {
  .t0,
  .t1,
  .t10,
  .t11,
  .t12,
  .t13,
  .t14,
  .t15,
  .t16,
  .t17,
  .t18,
  .t19,
  .t2,
  .t20,
  .t21,
  .t22,
  .t22,
  .t23,
  .t23,
  .t24,
  .t3,
  .t4,
  .t5,
  .t6,
  .t7,
  .t8,
  .t9 {
    background-size: 150%; } }



